/* SucessoPage.css */

.app-container {
    font-family: Arial, sans-serif;
    color: #333;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.top-bar {
    background-color: #000;
    color: #888;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 20px;
    font-size: 12px;
}

.top-right-icons svg {
    margin-left: 10px;
    cursor: pointer;
}

.main-bar {
    background-color: #f5f5f5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 40px;
}

.main-left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.evandro {
    font-size: 24px;
    font-weight: bold;
}

.neto {
    font-size: 16px;
}

.main-center .nav-item {
    margin: 0 15px;
    cursor: pointer;
    position: relative;
}

.main-center .nav-item::after {
    content: '';
    display: block;
    height: 2px;
    width: 0;
    background: #333;
    transition: width 0.3s;
    position: absolute;
    bottom: -4px;
    left: 0;
}

.main-center .nav-item:hover::after {
    width: 100%;
}

.main-right {
    display: flex;          
    flex-direction: row;   
    align-items: center;   
    gap: 15px;              
}

.hero-bar {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    padding: 60px;
}

.hero-left {
    max-width: 50%;
}

.hero-left h1 {
    font-size: 48px;
    margin-bottom: 20px;
}

.hero-left p {
    font-size: 20px;
    margin-bottom: 25px;
}

.ver-produtos-btn {
    padding: 12px 28px;
    background-color: #333;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 4px;
    font-size: 16px;
}

.hero-right .slideshow img {
    width: 400px;
    height: auto;
    border-radius: 8px;
}

.bottom-bar {
    background-color: #000;
    color: #fff;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 20px 40px;
    flex-wrap: wrap;
}

.bottom-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    font-size: 14px;
}

.bottom-item svg {
    font-size: 32px;
    margin-bottom: 8px;
}

.whatsapp-btn {
    background-color: #25D366;
    color: #fff;
    border: none;
    padding: 10px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    font-size: 24px;
    justify-content: center;
}


/*Log off*/
/* Overlay cobrindo tudo */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); /* preto semi-transparente */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}

/* Caixinha do modal */
.logoff-box {
    background-color: #fff;
    padding: 30px 20px;
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.4);
    width: 300px;
    text-align: center;
}

.logoff-title {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
}

/* Botões alinhados lado a lado */
.logoff-buttons {
    display: flex;
    justify-content: space-between;
    gap: 15px;
}

.logoff-buttons button {
    flex: 1;
    padding: 10px 0;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s;
}

.logoff-confirm {
    background-color: #4CAF50;
    color: white;
}

.logoff-confirm:hover {
    background-color: #45a049;
}

.logoff-cancel {
    background-color: #f44336;
    color: white;
}

.logoff-cancel:hover {
    background-color: #da190b;
}

/* Garantir que o ícone não desalinhe */
.user-icon {
    font-size: 20px;
    cursor: pointer;
}


/*Log off*/