
body {
    font-family: Arial, sans-serif;
    background-color: #f2f2f2;
    margin: 0;
    padding: 0;
}

.nova-conta {
    background-color: #fff;
    padding: 30px;
    margin: 50px auto;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    max-width: 600px;
}

.nova-conta h1, .nova-conta h2 {
    text-align: center;
    color: #333;
}

.nova-conta form {
    display: flex;
    flex-direction: column;
}

.nova-conta label {
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    color: #555;
}

.nova-conta input, .nova-conta select {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 5px;
    font-size: 14px;
}

.nova-conta button {
    padding: 12px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
    transition: background-color 0.3s;
}

.nova-conta button:hover {
    background-color: #45a049;
}

.nova-conta button:last-of-type {
    background-color: #f44336;
}

.nova-conta button:last-of-type:hover {
    background-color: #da190b;
}
